* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

.sidebar {
  z-index: 200;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  flex-direction: column;
  gap: .5rem;
  width: 45%;
  height: 100vh;
  padding: 2rem .5rem;
  transition: all .3s;
  display: flex;
  position: fixed;
  transform: translateX(-100%);
  box-shadow: 0 4px 8px #0000001a;
}

.sidebar-active {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

header {
  z-index: 100;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa.fa-bars {
  display: none;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.login-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover {
  color: #6b2da8;
}

.login-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

.section-contain {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 100vh;
  padding: 5rem 12%;
  display: flex;
}

.qr-details {
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: 1rem;
  width: 70%;
  height: 100%;
}

#reader {
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 100%;
  display: flex;
}

.detection-status {
  padding: 1rem;
}

.hidden {
  display: none;
}

@media (width <= 480px) {
  .sidebar-container {
    flex-direction: column;
    display: flex;
  }

  .sidebar-btns {
    border-radius: .5rem;
    gap: 1rem;
    width: 100%;
    padding: 1rem .5rem;
    font-size: 1.1rem;
    font-weight: 700;
    transition: all .3s;
    display: flex;
  }

  .sidebar-btns:hover, .sidebar-btns:hover i, .sidebar-btns:hover p {
    background-color: #e2dfe3;
  }

  .sidebar-btns i {
    color: #6b2da8;
    background-color: #fff;
    transition: all .3s;
  }

  .sidebar-btns p {
    transition: all .3s;
  }

  section {
    height: auto;
  }

  .fa.fa-bars {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .section-contain {
    flex-direction: column;
    height: auto;
    display: flex;
  }

  .detection-status {
    color: #fff;
    flex-direction: column;
    gap: .5rem;
    width: 100%;
    display: flex;
  }

  #reader {
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
  }
}
